import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { NgForm } from '@angular/forms';
import { CloudStorageProviderService } from 'src/app/services/cloud-storage-provider.service';
import { TeamService } from 'src/app/services/uiServices/team.service';


@Component({
  selector: 'app-add-or-edit-team-member',
  templateUrl: './add-or-edit-team-member.component.html',
  styleUrls: ['./add-or-edit-team-member.component.css']
})
export class AddOrEditTeamMemberComponent implements OnInit {

  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  memberId: string;
  isEdit: boolean;
  selectedMember: Presenter = {
    name: "",
    image: "",
    cv: "",
    position: ""
  };
  selectedIcon: any;
  iconUrl: string;
  constructor(
    private teamService: TeamService,
    private route: ActivatedRoute,
    private location: Location,
    private spinner: NgxSpinnerService,
    private cloudStorage: CloudStorageProviderService
  ) {
    this.route.params.subscribe(params => {
      this.memberId = params.id;
      this.isEdit = this.memberId != "0";
      console.log(this.memberId);
      this.getMemberDetails();
    });
  }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
  }
  async getMemberDetails() {
    if (this.isEdit) {
      try {
        this.spinner.show();
        this.selectedMember = await this.teamService.getMemberDetails(this.memberId);
        this.iconUrl = this.selectedMember.image;
        this.spinner.hide();
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.selectedMember = {
        name: "",
        image: "",
        cv: "",
        position: ""
      };
    }

  }
  onBackCliced() {
    this.location.back();
  }
  async save(form: NgForm) {
    console.log(form);
    if (form.valid && (this.selectedIcon != null || this.iconUrl)) {
      this.spinner.show();
      if (this.selectedIcon) {
        this.selectedMember.image = await this.cloudStorage.uploadFile(this.selectedIcon, this.selectedIcon.target.files[0].name);
      }
      if (this.isEdit) {
        this.teamService.updateMember(this.selectedMember).then((isDone) => {
          this.spinner.hide();
          this.successfulDialog.show().then(value => {
            this.onBackCliced();
          });
        },
          error => {
            this.spinner.hide();
            this.errorDialog.options = {
              title: "      "
            };
            this.errorDialog.show();
          });
      }
      else {
        this.teamService.createMember(this.selectedMember).then((isDone) => {
          this.spinner.hide();
          this.successfulDialog.show().then(value => {
            this.onBackCliced();
          });
        },
          error => {
            this.spinner.hide();
            this.errorDialog.options = {
              title: "      "
            };
            this.errorDialog.show();
          });
      }

    }
    else {
      this.errorDialog.options = {
        title: "     ",
        confirmButtonText: ""
      };
      this.errorDialog.show();
    }
  }
  onSelectedFile(event: any) {
    console.log("Selected file data");
    console.log(event.target.files[0].name);
    console.log(event.target.files[0]);
    this.selectedIcon = event;
  }
  deleteFile() {
    this.selectedIcon = null;
  }

}
