import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';

@Component({
  selector: 'app-event-details',
  templateUrl: './event-details.component.html',
  styleUrls: ['./event-details.component.css']
})
export class EventDetailsComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('deleteSwal') private deleteSwal: SwalComponent;
  eventId: string;
  selectedEvent: TvEvent = {
    name: "",
    duration: "",
    icon: "",
    isShowen: false,
    schedules: [],
    banner: ""
  };
  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private programService: ProgramsService,
    private spinner: NgxSpinnerService
  ) {
    this.route.params.subscribe(params => {
      this.eventId = params.id;
      console.log(this.eventId);
    });
  }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
    this.getEventDetails();
  }

  async getEventDetails() {
    try {
      this.spinner.show();
      this.selectedEvent = await this.programService.getEventDetails(this.eventId);
      this.spinner.hide();
    }
    catch (error) {
      this.spinner.hide();
      this.errorDialog.show();
    }

  }
  getPublishTime(time: string) {
    return time != null ? time.substr(time.indexOf("T") + 1, 5) : "";
  }
  async deleteSchedule(scheduleId: string) {
    try {
      this.deleteSwal.options = {
        title: "  ",
        type: "warning",
        showCancelButton: true,
        cancelButtonText: "",
        confirmButtonText: ""
      };
      this.deleteSwal.show().then(value => {
        console.log(value);
        if (value.value === true) {
          this.programService.deleteSchedule(scheduleId).then(success => {
            this.getEventDetails();
          }, error => {
            console.log(error);
            this.errorDialog.show();
          }
          );
        }
      });
    }
    catch (error) {
      this.errorDialog.show();
    }
  }
}
