  async getSettings(): Promise<Settings> {
    this.userData = this.storage.get(Constants.USER_DATA);
    return new Promise<Settings>((resolve, reject) => {
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": Constants.SETTINGS_QUERY }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.settings) {
          resolve(response.data.settings[0]);
        }
        else {
          reject("Failed to load channel settings");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
