  async updateSpecialBanner(banner: SpecialBanner, isEvent: boolean, enitityId: string) {
    this.userData = this.storage.get(Constants.USER_DATA);
    let programDisconnectQuery: string = banner.program != null ? ` program:{
      disconnect:true
    }` : "";
    let eventDisconnectQuery: string = banner.event != null ? `event:{disconnect:true}` : "";

    let query = isEvent ? `mutation{
      updateSpecialBanner(
        where:{id:"${banner.id}"},
        data:{
         ${programDisconnectQuery}
          event:{connect:{id:"${enitityId}"}}
          
        }
      ){
       id  
      }
    }`: `
    mutation{
      updateSpecialBanner(
        where:{id:"${banner.id}"},
        data:{
          program:{
            connect:{id:"${enitityId}"}
          }
          ${eventDisconnectQuery}
        }
      ){
       id  
      }
    }`;
    console.log(query);
    return new Promise<boolean>((resolve, reject) => {
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.updateSpecialBanner) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
