  async updateSpecialPrograms(settingsId: string, firstProgramId: string, secondProgramId: string, thirdProgramId: string, fourthProgramId: string, fivethProgramId: string, sixthProgramId: string, priviousPrograms: Array<Program>) {
    this.userData = this.storage.get(Constants.USER_DATA);
    let disconnectQuery = "[]";
    switch (priviousPrograms.length) {
      case 0:
        break;
      case 1:
        disconnectQuery = `[{id:"${priviousPrograms[0].id}"}]`;
        break;
      case 2:
        disconnectQuery = `[{id:"${priviousPrograms[0].id}"}, {id:"${priviousPrograms[1].id}"}]`;
        break;
      case 3:
        disconnectQuery = `[{id:"${priviousPrograms[0].id}"}, {id:"${priviousPrograms[1].id}"}, {id:"${priviousPrograms[2].id}"}]`;
        break;
      case 4:
        disconnectQuery = `[{id:"${priviousPrograms[0].id}"}, {id:"${priviousPrograms[1].id}"}, {id:"${priviousPrograms[2].id}"}, {id:"${priviousPrograms[3].id}"}]`;
        break;
      case 5:
        disconnectQuery = `[{id:"${priviousPrograms[0].id}"}, {id:"${priviousPrograms[1].id}"}, {id:"${priviousPrograms[2].id}"}, {id:"${priviousPrograms[3].id}"}, {id:"${priviousPrograms[4].id}"}]`;
        break;
      case 6:
        disconnectQuery = `[{id:"${priviousPrograms[0].id}"}, {id:"${priviousPrograms[1].id}"}, {id:"${priviousPrograms[2].id}"}, {id:"${priviousPrograms[3].id}"}, {id:"${priviousPrograms[4].id}"},{id:"${priviousPrograms[5].id}"}]`;
        break;
    }
    let query = `mutation{
      updateSetting(where:{id:"${settingsId}"},data:{
        specialPrograms:{
          disconnect:${disconnectQuery}
        }
      }){
        specialPrograms{
          id,name
        }
      }
    }`;
    return new Promise<boolean>((resolve, reject) => {
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.updateSetting) {
          let query2 = `mutation{
            updateSetting(where:{id:"${settingsId}"},data:{
              specialPrograms:{
                connect:[{id:"${firstProgramId}"},{id:"${secondProgramId}"},{id:"${thirdProgramId}"},{id:"${fourthProgramId}"},{id:"${fivethProgramId}"},{id:"${sixthProgramId}"}]
              }
            }){
              specialPrograms{
                id,name
              }
            }
          }`;
          this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query2 }).subscribe((response) => {
            console.log(response);
            if (response.data && response.data.updateSetting) {
              resolve(true);
            }
            else {
              reject("Please check your internet connection");
            }
          }, (error) => {
            console.log(error);
            reject("Please check your internet connection");
          });
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
