  async performLogin(loginFormValue: { username: string, password: string }): Promise<LoginResponse> {
    let query = `mutation{
      login(username:"${loginFormValue.username}",password:"${loginFormValue.password}"){
        userId
        token
      }
    }`;
    return new Promise<LoginResponse>((resolve, reject) => {
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.login) {
          resolve(response.data.login);
        }
        else {
          reject(response.errors[0].message);
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  escapeDoubleQuotes(str) {
    return str.replace(/\\([\s\S])|(")/g, "\\$1$2"); // thanks @slevithan!
  }
