import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';
import { NgForm } from '@angular/forms';
import { DatepickerOptions } from 'ng2-datepicker';

@Component({
  selector: 'app-edit-event-schedule',
  templateUrl: './edit-event-schedule.component.html',
  styleUrls: ['./edit-event-schedule.component.css']
})
export class EditEventScheduleComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  eventId: string;
  secheduleId: string;
  isEdit: boolean;
  selectedEvent: TvEvent;
  selectedSchedule: Schedule;
  systemDays: Array<Day> = [];
  friId: boolean;
  thuId: boolean;
  wedId: boolean;
  tueId: boolean;
  monId: boolean;
  sunId: boolean;
  satId: boolean;
  options: DatepickerOptions = {
    minYear: 1970,
    maxYear: 2030,
    displayFormat: 'DD-MM-YYYY',
    barTitleFormat: 'MMMM YYYY',
    dayNamesFormat: 'dd',
    firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
    barTitleIfEmpty: '  ',
    placeholder: '  ', // HTML input placeholder attribute (default: '')
    fieldId: 'my-date-picker',
    useEmptyBarTitle: false,

  };
  originalScheduleDays: Array<Day>;
  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private programService: ProgramsService,
    private spinner: NgxSpinnerService,
  ) {
    this.selectedSchedule = {
      days: [],
      endDate: "",
      finishTimeDate: "",
      startDate: "",
      timeDate: "",
      type: "new"
    }
  }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
    this.route.params.subscribe(params => {
      this.eventId = params.eventId;
      this.secheduleId = params.scheduleId;
      this.isEdit = this.secheduleId != "0";
      console.log(this.eventId);
      console.log(this.secheduleId);
      this.getProgramDetails();
    });
  }
  async getProgramDetails() {

    try {
      this.spinner.show();
      this.selectedEvent = await this.programService.getEventDetails(this.eventId);
      this.systemDays = await this.programService.getDaysList();
      this.spinner.hide();
      if (this.isEdit) {
        this.selectedSchedule = this.selectedEvent.schedules.find(item => item.id == this.secheduleId);
        console.log("Selected schedule");
        console.log(this.selectedSchedule);
        this.selectedSchedule.timeDate = this.selectedSchedule.timeDate.substr(this.selectedSchedule.timeDate.indexOf("T") + 1, 5);
        this.originalScheduleDays = this.selectedSchedule.days;
        this.satId = this.selectedSchedule.days.find(day => day.name == "Sat") != undefined;
        this.sunId = this.selectedSchedule.days.find(day => day.name == "Sun") != undefined;
        this.monId = this.selectedSchedule.days.find(day => day.name == "Mon") != undefined;
        this.tueId = this.selectedSchedule.days.find(day => day.name == "Tue") != undefined;
        this.wedId = this.selectedSchedule.days.find(day => day.name == "Wed") != undefined;
        this.thuId = this.selectedSchedule.days.find(day => day.name == "Thu") != undefined;
        this.friId = this.selectedSchedule.days.find(day => day.name == "Fri") != undefined;
      }
    }
    catch (error) {
      console.log(error);
      this.spinner.hide();
      this.errorDialog.options = {
        title: "   "
      };
      this.errorDialog.show();
    }

  }
  onBackCliced() {
    this.location.back();
  }
  async save(form: NgForm) {
    console.log(form.value);
    if (form.valid) {
      try {
        this.spinner.show();
        if (this.isEdit) {
          await this.programService.updateProgramSchedule(this.selectedSchedule.id, form, this.selectedEvent, this.systemDays, this.originalScheduleDays);
          this.spinner.hide();
          this.successfulDialog.show().then(value => {
            this.onBackCliced();
          });

        }
        else {
          await this.programService.createProgramSchedule(form, this.selectedEvent, this.systemDays, true);
          this.spinner.hide();
          this.successfulDialog.show().then(value => {
            this.onBackCliced();
          });

        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.options = {
          title: "   "
        };
        this.errorDialog.show();
      }


    }
    else {
      this.errorDialog.options = {
        title: "     "
      };
      this.errorDialog.show();
    }
  }
}
