import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { NgForm } from '@angular/forms';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { Router } from "@angular/router";
import { Constants } from 'src/app/Constants';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.css']
})
export class LoginPageComponent implements OnInit {

  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  loginObject: any = {
    username: "",
    password: ""
  }
  constructor(
    private homeService: HomePageServicesService,
    private spinner: NgxSpinnerService,
    private router: Router,
    @Inject(SESSION_STORAGE) private storage: StorageService,
  ) { }

  ngOnInit() {
    this.errorDialog.options = {
      confirmButtonText: ""
    };
  }
  save(loginForm: NgForm) {
    console.log("Save");
    console.log(loginForm);
    if (loginForm.valid) {
      this.homeService.performLogin(this.loginObject).then(response => {
        console.log(response);
        this.storage.set(Constants.USER_DATA, response);
        this.router.navigateByUrl("home");
      }, error => {
        this.errorDialog.options = {
          title: error.toString()
        };
        this.errorDialog.show();
      })
    }
    else {
      this.errorDialog.options = {
        title: "    "
      };
      this.errorDialog.show();
    }
  }
}
