import { Component, OnInit, ViewChild } from '@angular/core';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { NgForm } from '@angular/forms';
import { ServiceHandlerProvider } from 'src/app/services/service-handler.service';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.css']
})
export class NotificationComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  constructor(private serviceHandler: ServiceHandlerProvider) {

  }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
  }
  save(notificationForm: NgForm) {
    if (notificationForm.valid) {
      console.log(notificationForm.value);
      var request = {
        "to": "/topics/ALLUSERS",
        "notification": {
          "title": notificationForm.value.title,
          "body": notificationForm.value.body
        },
        "data": {
          "title": notificationForm.value.title,
          "body": notificationForm.value.body,
          "itemId": notificationForm.value.itemId,
          "itemType": notificationForm.value.itemType
        }
      };
      console.log("Notification request" + request);
      var headers = {
        "Content-Type": "application/json",
        "Authorization": "key=AIzaSyAmo7plaxMf7dzGhfQCqDZJNgI5ofyvDAY"
      };

      this.serviceHandler.runService("https://fcm.googleapis.com/fcm/send", "POST", null, request, headers).subscribe((onResponse) => {
        this.successfulDialog.show();
        console.log(onResponse);
      }, (error) => {
        console.log(error);
        this.errorDialog.show();
      });
    }
  }
}
