import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { NgForm } from '@angular/forms';
import { CloudStorageProviderService } from 'src/app/services/cloud-storage-provider.service';
import { LinksService } from 'src/app/services/uiServices/links.service';

@Component({
  selector: 'app-add-or-edit-links',
  templateUrl: './add-or-edit-links.component.html',
  styleUrls: ['./add-or-edit-links.component.css']
})
export class AddOrEditLinksComponent implements OnInit {

  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  linkId: string;
  isEdit: boolean;
  selectedLink: Link = {
    name: "",
    icon: "",
    url: ""
  };
  selectedIcon: any;
  iconUrl: string;
  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private linksService: LinksService,
    private spinner: NgxSpinnerService,
    private cloudStorage: CloudStorageProviderService
  ) {
    this.route.params.subscribe(params => {
      this.linkId = params.id;
      this.isEdit = this.linkId != "0";
      console.log(this.linkId);
      this.getLinkDetails();
    });
  }
  async getLinkDetails() {
    if (this.isEdit) {
      try {
        this.spinner.show();
        this.selectedLink = await this.linksService.getLinkDetails(this.linkId);
        this.iconUrl = this.selectedLink.icon;
        this.spinner.hide();
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.selectedLink = {
        name: "",
        icon: "",
        url: ""
      };
    }

  }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };

  }
  onBackCliced() {
    this.location.back();
  }
  async save(form: NgForm) {
    console.log(form);
    if (form.valid && (this.selectedIcon != null || this.iconUrl)) {
      this.spinner.show();
      if (this.selectedIcon) {
        this.selectedLink.icon = await this.cloudStorage.uploadFile(this.selectedIcon, this.selectedIcon.target.files[0].name);
      }
      if (this.isEdit) {
        this.linksService.updateLink(this.selectedLink).then((isDone) => {
          this.spinner.hide();
          this.successfulDialog.show().then(value => {
            this.onBackCliced();
          });
        },
          error => {
            this.spinner.hide();
            this.errorDialog.options = {
              title: "      "
            };
            this.errorDialog.show();
          });
      }
      else {
        this.linksService.createLink(this.selectedLink).then((isDone) => {
          this.spinner.hide();
          this.successfulDialog.show().then(value => {
            this.onBackCliced();
          });
        },
          error => {
            this.spinner.hide();
            this.errorDialog.options = {
              title: "      "
            };
            this.errorDialog.show();
          });
      }

    }
    else {
      this.errorDialog.options = {
        title: "     ",
        confirmButtonText: ""
      };
      this.errorDialog.show();
    }
  }
  onSelectedFile(event: any) {
    console.log("Selected file data");
    console.log(event.target.files[0].name);
    console.log(event.target.files[0]);
    this.selectedIcon = event;
  }
  deleteFile() {
    this.selectedIcon = null;
  }

}
