import { Injectable, Inject } from '@angular/core';
import { ServiceHandlerProvider } from '../service-handler.service';
import { Constants } from 'src/app/Constants';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';

@Injectable({
  providedIn: 'root'
})
export class TeamService {
  userData: LoginResponse;

  constructor(private serviceHadler: ServiceHandlerProvider, @Inject(SESSION_STORAGE) private storage: StorageService) {
    this.userData = this.storage.get(Constants.USER_DATA);
  }

  async getTeam(): Promise<Array<Presenter>> {
    return new Promise<Array<Presenter>>((resolve, reject) => {
      let query = `query{
        presenters{
          id,
          name,
          position,
          cv,
          image
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.presenters) {
          resolve(response.data.presenters);
        }
        else {
          reject("Failed to load team members");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async deleteMember(memberId: string): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        deletePresenter(where:{id:"${memberId}"}){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.deletePresenter) {
          resolve(true);
        }
        else {
          reject("Failed to delete link");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async getMemberDetails(memberId: string): Promise<Presenter> {
    return new Promise<Presenter>((resolve, reject) => {
      let query = `query{
        presenter(where:{
          id:"${memberId}"
        })
        {
          id,
          name,
          position,
          cv,
          image
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.presenter) {
          resolve(response.data.presenter);
        }
        else {
          reject("Failed to load presenter");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async createMember(member: Presenter): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        createPresenter(
          data:{
            name:"${member.name}",
            position:"${member.position}"
            cv:"${member.cv}",
            image:"${member.image}"
          }
        ){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.createPresenter) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async updateMember(member: Presenter): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        updatePresenter(
          where:{id:"${member.id}"},
          data:{
            name:"${member.name}",
            position:"${member.position}"
            cv:"${member.cv}",
            image:"${member.image}"
          }
        ){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.updatePresenter) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
}
