import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { TeamService } from 'src/app/services/uiServices/team.service';

@Component({
  selector: 'app-our-team',
  templateUrl: './our-team.component.html',
  styleUrls: ['./our-team.component.css']
})
export class OurTeamComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('deleteSwal') private deleteSwal: SwalComponent;
  members: Array<Presenter>;
  constructor(private spinner: NgxSpinnerService, private teamService: TeamService) {

  }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
    this.loadTeam();
  }
  async deleteMember(memberId: string) {
    try {
      this.deleteSwal.options = {
        title: "   ",
        type: "warning",
        showCancelButton: true,
        cancelButtonText: "",
        confirmButtonText: ""
      };
      this.deleteSwal.show().then(value => {
        console.log(value);
        if (value.value === true) {
          this.teamService.deleteMember(memberId).then(success => {
            this.loadTeam();
          }, error => {
            console.log(error);
            this.errorDialog.show();
          }
          );
        }
      });
    }
    catch (error) {
      this.errorDialog.show();
    }
  }
  async loadTeam() {
    try {
      this.spinner.show();
      this.members = await this.teamService.getTeam();
      this.spinner.hide();
    }
    catch (errror) {
      console.log(errror);
      this.spinner.hide();
      this.errorDialog.show();
    }
  }

}
