import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';
import { NgForm } from '@angular/forms';
import { CloudStorageProviderService } from 'src/app/services/cloud-storage-provider.service';


@Component({
  selector: 'app-edit-event',
  templateUrl: './edit-event.component.html',
  styleUrls: ['./edit-event.component.css']
})
export class EditEventComponent implements OnInit {

  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  eventId: string;
  isEdit: boolean;
  selectedEvent: TvEvent = {
    name: "",
    banner: "",
    duration: "",
    icon: "",
    isShowen: true,

  };
  selectedWebBanner: any;
  selectedIcon: any;
  webBannerUrl: string;
  iconUrl: string;
  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private programService: ProgramsService,
    private spinner: NgxSpinnerService,
    private cloudStorage: CloudStorageProviderService
  ) {
    this.route.params.subscribe(params => {
      this.eventId = params.id;
      this.isEdit = this.eventId != "0";
      console.log(this.eventId);
    });
  }

  async getEventDetails() {
    if (this.isEdit) {
      try {
        this.spinner.show();
        this.selectedEvent = await this.programService.getEventDetails(this.eventId);
        this.webBannerUrl = this.selectedEvent.banner;
        this.iconUrl = this.selectedEvent.icon;
        this.spinner.hide();
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.selectedEvent = {
        name: "",
        banner: "",
        duration: "",
        icon: "",
        isShowen: true,
      };
    }

  }
  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
    this.getEventDetails();
  }
  onBackCliced() {
    this.location.back();
  }
  async save(form: NgForm) {
    console.log(form);
    if (form.valid) {
      if (this.webBannerUrl || this.selectedWebBanner) {
        if (this.iconUrl || this.selectedIcon) {
          this.spinner.show();
          if (this.selectedWebBanner) {
            this.selectedEvent.banner = await this.cloudStorage.uploadFile(this.selectedWebBanner, this.selectedWebBanner.target.files[0].name);
          }
          if (this.selectedIcon) {
            this.selectedEvent.icon = await this.cloudStorage.uploadFile(this.selectedIcon, this.selectedIcon.target.files[0].name);
          }
          if (this.isEdit) {
            this.programService.updateEvent(this.selectedEvent).then((isDone) => {
              this.spinner.hide();
              this.successfulDialog.show().then(value => {
                this.onBackCliced();
              });
            },
              error => {
                this.spinner.hide();
                this.errorDialog.options = {
                  title: "      "
                };
                this.errorDialog.show();
              });
          }
          else {
            this.programService.createEvent(this.selectedEvent).then((isDone) => {
              this.spinner.hide();
              this.successfulDialog.show().then(value => {
                this.onBackCliced();
              });
            },
              error => {
                this.spinner.hide();
                this.errorDialog.options = {
                  title: "      "
                };
                this.errorDialog.show();
              });
          }


        }
        else {
          this.errorDialog.options = {
            title: "    ",
            confirmButtonText: ""
          };
          this.errorDialog.show();
        }

      }
      else {
        this.errorDialog.options = {
          title: "    ",
          confirmButtonText: ""
        };
        this.errorDialog.show();
      }

    }
    else {
      this.errorDialog.options = {
        title: "     ",
        confirmButtonText: ""
      };
      this.errorDialog.show();
    }
  }
  onSelectedFile(event: any, tag: string) {
    console.log("Selected file data");
    console.log(event.target.files[0].name);
    console.log(event.target.files[0]);
    switch (tag) {
      case 'web':
        this.selectedWebBanner = event;
        break;
      case 'icon':
        this.selectedIcon = event;
        break;
    }
  }
  deleteFile(tag: string) {
    switch (tag) {
      case 'web':
        this.selectedWebBanner = null;
        break;
      case 'icon':
        this.selectedIcon = null;
        break;
    }
  }

}
