import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';
import { Router } from '@angular/router';
declare var $: any;

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent implements OnInit {

  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('deleteSwal') private deleteSwal: SwalComponent;
  events: Array<TvEvent>;
  constructor(private programsService: ProgramsService, private spinner: NgxSpinnerService, private router: Router) { }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
    this.loadEvents();
  }
  async loadEvents() {
    try {
      this.spinner.show();
      this.events = await this.programsService.getEventsList();
      this.spinner.hide();

    }
    catch (errror) {
      this.spinner.hide();
      this.errorDialog.show();
    }
  }
  async changeAppearanceStatus(event: TvEvent) {
    try {
      await this.programsService.changeEventAppearanceStatus(event.id, event.isShowen ? false : true);
      this.loadEvents();
    }
    catch (error) {
      this.errorDialog.show();
    }
  }
  async deleteEvent(eventId: string) {
    try {
      console.log("Event id to delete: " + eventId);
      this.deleteSwal.options = {
        title: "  ",
        type: "warning",
        showCancelButton: true,
        cancelButtonText: "",
        confirmButtonText: ""
      };
      this.deleteSwal.show().then(value => {
        console.log(value);
        if (value.value === true) {
          this.programsService.deleteEvent(eventId).then(success => {
            this.loadEvents();
          }, error => {
            console.log(error);
            this.errorDialog.show();
          }
          );
        }
      });

    }
    catch (error) {
      this.errorDialog.show();
    }
  }
  addNewEvent() {
    this.router.navigateByUrl("/editEvent/0");
  }

}
