  async createProgramSchedule(scheduleForm: NgForm, selectedProgram: Program | TvEvent, days: Array<Day>, isEvent?: boolean): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let timeDate = `1970-01-01T${scheduleForm.value.timeDate}:00.000Z`;
      let finishTimeDate = moment(timeDate).add(selectedProgram.duration, 'minutes').toISOString();
      let connectQuery = "";
      if (scheduleForm.value.sat) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Sat").id}"},`;
      }
      if (scheduleForm.value.sun) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Sun").id}"},`;
      }
      if (scheduleForm.value.mon) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Mon").id}"},`;
      }
      if (scheduleForm.value.tue) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Tue").id}"},`;
      }
      if (scheduleForm.value.wed) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Wed").id}"},`;
      }
      if (scheduleForm.value.thu) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Thu").id}"},`;
      }
      if (scheduleForm.value.fri) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Fri").id}"},`;
      }

      let query = isEvent ? `mutation{
        createSchedule(
          data:{
          timeDate:"${timeDate}",
          finishTimeDate:"${finishTimeDate}",
          startDate:"${moment(scheduleForm.value.startDate).toISOString(true)}",
          endDate:"${moment(scheduleForm.value.endDate).toISOString(true)}",
          type:"${scheduleForm.value.type}",
          event:{
            connect:{id:"${selectedProgram.id}"}
          }
          days:{
            connect:[${connectQuery}],
          }
        }){
          id
        }
      }`: `mutation{
        createSchedule(
          data:{
          timeDate:"${timeDate}",
          finishTimeDate:"${finishTimeDate}",
          startDate:"${moment(scheduleForm.value.startDate).toISOString(true)}",
          endDate:"${moment(scheduleForm.value.endDate).toISOString(true)}",
          type:"${scheduleForm.value.type}",
          program:{
            connect:{id:"${selectedProgram.id}"}
          }
          days:{
            connect:[${connectQuery}],
          }
        }){
          id
        }
      }`;
      console.log(query.toString());
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.createSchedule) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async updateProgramSchedule(scheduleId: string, scheduleForm: NgForm, selectedProgram: Program | TvEvent, days: Array<Day>, originalDays: Array<Day>): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let timeDate = `1970-01-01T${scheduleForm.value.timeDate}:00.000Z`;
      let finishTimeDate = moment(timeDate).add(selectedProgram.duration, 'minutes').toISOString();
      let connectQuery = "";
      let disconnectQuery = "";
      if (scheduleForm.value.sat) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Sat").id}"},`;
      }
      else {
        if (originalDays.find(day => day.name == "Sat"))
          disconnectQuery = disconnectQuery + `{id:"${days.find(day => day.name == "Sat").id}"},`;

      }
      if (scheduleForm.value.sun) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Sun").id}"},`;
      }
      else {
        if (originalDays.find(day => day.name == "Sun"))
          disconnectQuery = disconnectQuery + `{id:"${days.find(day => day.name == "Sun").id}"},`;

      }
      if (scheduleForm.value.mon) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Mon").id}"},`;
      }
      else {
        if (originalDays.find(day => day.name == "Mon"))
          disconnectQuery = disconnectQuery + `{id:"${days.find(day => day.name == "Mon").id}"},`;

      }
      if (scheduleForm.value.tue) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Tue").id}"},`;
      }
      else {
        if (originalDays.find(day => day.name == "Tue"))
          disconnectQuery = disconnectQuery + `{id:"${days.find(day => day.name == "Tue").id}"},`;
      }
      if (scheduleForm.value.wed) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Wed").id}"},`;
      }
      else {
        if (originalDays.find(day => day.name == "Wed"))
          disconnectQuery = disconnectQuery + `{id:"${days.find(day => day.name == "Wed").id}"},`;

      }
      if (scheduleForm.value.thu) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Thu").id}"},`;
      }
      else {
        if (originalDays.find(day => day.name == "Thu"))
          disconnectQuery = disconnectQuery + `{id:"${days.find(day => day.name == "Thu").id}"},`;

      }
      if (scheduleForm.value.fri) {
        connectQuery = connectQuery + `{id:"${days.find(day => day.name == "Fri").id}"},`;
      }
      else {
        if (originalDays.find(day => day.name == "Fri"))
          disconnectQuery = disconnectQuery + `{id:"${days.find(day => day.name == "Fri").id}"},`;

      }

      let query = `mutation{
        updateSchedule(
          where:{id:"${scheduleId}"},
          data:{
          timeDate:"${timeDate}",
          finishTimeDate:"${finishTimeDate}",
          startDate:"${moment(scheduleForm.value.startDate).toISOString(true)}",
          endDate:"${moment(scheduleForm.value.endDate).toISOString(true)}",
          type:"${scheduleForm.value.type}",
          days:{
            disconnect:[${disconnectQuery}]
            connect:[${connectQuery}],
          }
        }){
          id
        }
      }`;
      console.log(query.toString());
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.updateSchedule) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }

