import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { CloudStorageProviderService } from 'src/app/services/cloud-storage-provider.service';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('imageValidation') private imageValidation: SwalComponent;
  selectedChannelLogo: any = null;
  selectedArticlesLogo: any = null;
  selectedsportsLogo: any = null;
  selectedprogramsLogo: any = null;
  selectedteamLogo: any = null;
  selectedVideoLogo: any = null;
  androidUrl: string = null;
  iosUrl: string = null;
  settings: Settings;
  programs: Array<Program>;
  selectedNewsProgramId: string = "";
  specialBanners: Array<SpecialBanner> = [];
  privousSpecialBanners: Array<Program>;
  firstBannerId: string = "";
  secondBannerId: string = "";
  thirdBannerId: string = "";
  fourthBannerId: string = "";
  fivethBannerId: string = "";
  sixthBannerId: string = "";
  specialPrograms: Array<SpecialPrograms> = [];
  privousSpecialPrograms: Array<Program>;
  firstProgramId: string = "";
  secondProgramId: string = "";
  thirdProgramId: string = "";
  fourthProgramId: string = "";
  fivethProgramId: string = "";
  sixthProgramId: string = "";
  channelId: string;
  specialEntities: Array<SpecialEntity>;
  fbUrl: string;
  twitterUrl: string;
  instaUrl: string;
  youtubeUrl: string;
  constructor(private spinner: NgxSpinnerService, private cloudStorage: CloudStorageProviderService, private uiServicesProvider: HomePageServicesService) {
    this.loadSettings();
    this.loadPrograms();
    this.loadSpecialBanners();
    this.loadSpecialPrograms();
  }
  async loadSettings() {
    this.settings = await this.uiServicesProvider.getSettings();
    console.log("Project settings");
    console.log(this.settings);
    this.androidUrl = this.settings.infoDaTa.andUrl;
    this.iosUrl = this.settings.infoDaTa.iosUrl;
    this.channelId = this.settings.channelId;
    this.fbUrl = this.settings.infoDaTa.fbUrl;
    this.twitterUrl = this.settings.infoDaTa.twUrl;
    this.youtubeUrl = this.settings.infoDaTa.ytUrl;
    this.instaUrl = this.settings.infoDaTa.instUrl;

    if (this.settings.newsPrograms) {
      this.selectedNewsProgramId = this.settings.newsPrograms.id;
    }
  }

  async loadPrograms() {
    this.programs = await this.uiServicesProvider.getPrograms();
    this.specialEntities = await this.uiServicesProvider.getSpecialEntities();
  }
  async loadSpecialBanners() {
    this.specialBanners = await this.uiServicesProvider.getSpecialBanners();
    // this.privousSpecialBanners = this.settings.specialBanners;
    console.log("Special banners copy");
    console.log(this.specialBanners);
    this.firstBannerId = this.specialBanners[0].program ? this.specialBanners[0].program.id : this.specialBanners[0].event.id;
    this.secondBannerId = this.specialBanners[1].program ? this.specialBanners[1].program.id : this.specialBanners[1].event.id;
    this.thirdBannerId = this.specialBanners[2].program ? this.specialBanners[2].program.id : this.specialBanners[2].event.id;
    this.fourthBannerId = this.specialBanners[3].program ? this.specialBanners[3].program.id : this.specialBanners[3].event.id;
    this.fivethBannerId = this.specialBanners[4].program ? this.specialBanners[4].program.id : this.specialBanners[4].event.id;
    this.sixthBannerId = this.specialBanners[5].program ? this.specialBanners[5].program.id : this.specialBanners[5].event.id;
  }
  async loadSpecialPrograms() {
    this.specialPrograms = await this.uiServicesProvider.getSpecialPrograms();
    console.log("Special programs copy");
    console.log(this.specialPrograms);
    this.firstProgramId = this.specialPrograms[0] && this.specialPrograms[0].program ? this.specialPrograms[0].program.id : "";
    this.secondProgramId = this.specialPrograms[1] && this.specialPrograms[1].program ? this.specialPrograms[1].program.id : "";
    this.thirdProgramId = this.specialPrograms[2] && this.specialPrograms[2].program ? this.specialPrograms[2].program.id : "";
    this.fourthProgramId = this.specialPrograms[3] && this.specialPrograms[3].program ? this.specialPrograms[3].program.id : "";
    this.fivethProgramId = this.specialPrograms[4] && this.specialPrograms[4].program ? this.specialPrograms[4].program.id : "";
    this.sixthProgramId = this.specialPrograms[5] && this.specialPrograms[5].program ? this.specialPrograms[5].program.id : "";
  }
  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
    this.imageValidation.options = {
      confirmButtonText: ""
    };
  }
  async saveChannelLogo() {
    if (this.selectedChannelLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedChannelLogo, "channelLogo");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "logo", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveArticlesLogo() {
    if (this.selectedArticlesLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedArticlesLogo, "articlesLogo");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "articlesIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveSportsLogo() {
    if (this.selectedsportsLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedsportsLogo, "sportsLogo");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "sportsArticleIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveProgramsLogo() {
    if (this.selectedprogramsLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedprogramsLogo, "programsLogo");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "programIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveTeamLogo() {
    if (this.selectedteamLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedteamLogo, "teamIcon");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "teamIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  async saveVideolLogo() {
    if (this.selectedVideoLogo) {
      this.spinner.show();
      try {
        const uploadUrl = await this.cloudStorage.uploadFile(this.selectedVideoLogo, "videoIcon");
        const done = await this.uiServicesProvider.updateLogo(this.settings.id, "videoIcon", uploadUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.show();
    }

  }
  onSelectedFile(event: any, tag: string) {
    console.log("Selected file data");
    console.log(event.target.files[0].name);
    console.log(event.target.files[0]);
    switch (tag) {
      case 'channel':
        this.selectedChannelLogo = event;
        break;
      case 'articles':
        this.selectedArticlesLogo = event;
        break;
      case 'sports':
        this.selectedsportsLogo = event;
        break;
      case 'programs':
        this.selectedprogramsLogo = event;
        break;
      case 'team':
        this.selectedteamLogo = event;
        break;
      case 'video':
        this.selectedVideoLogo = event;
        break;
    }
  }
  deleteFile(tag: string) {
    switch (tag) {
      case 'channel':
        this.selectedChannelLogo = null;
        break;
      case 'articles':
        this.selectedArticlesLogo = null;
        break;
      case 'sports':
        this.selectedsportsLogo = null;
        break;
      case 'programs':
        this.selectedprogramsLogo = null;
        break;
      case 'team':
        this.selectedteamLogo = null;
        break;
      case 'video':
        this.selectedVideoLogo = null;
        break;
    }
  }
  async updateAppsUrl() {
    if (this.iosUrl && this.iosUrl != "" && this.androidUrl && this.androidUrl != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateAppsUrl(this.settings.id, this.androidUrl, this.iosUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "     "
      }
      this.imageValidation.show();
    }
  }
  async updateSocialMediaLinks() {
    if (this.fbUrl && this.fbUrl != "" && this.twitterUrl && this.twitterUrl != "" && this.youtubeUrl && this.youtubeUrl != "" && this.instaUrl && this.instaUrl != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateSocialMediaLinks(this.settings.id, this.fbUrl, this.twitterUrl, this.instaUrl, this.youtubeUrl);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "     "
      }
      this.imageValidation.show();
    }
  }
  async updateNewsProgram() {
    if (this.selectedNewsProgramId != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateNewsProgram(this.settings.id, this.selectedNewsProgramId);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "     "
      }
      this.imageValidation.show();
    }
  }
  async updateSpecialBanners() {
    if (this.firstBannerId != "" && this.secondBannerId != "" && this.thirdBannerId != "" && this.fourthBannerId != "" && this.fivethBannerId != "" && this.sixthBannerId != "") {
      this.spinner.show();
      try {
        await this.uiServicesProvider.updateSpecialBanner(this.specialBanners[0], this.specialEntities.find(entity => entity.id == this.firstBannerId).type != null, this.firstBannerId);
        await this.uiServicesProvider.updateSpecialBanner(this.specialBanners[1], this.specialEntities.find(entity => entity.id == this.secondBannerId).type != null, this.secondBannerId);
        await this.uiServicesProvider.updateSpecialBanner(this.specialBanners[2], this.specialEntities.find(entity => entity.id == this.thirdBannerId).type != null, this.thirdBannerId);
        await this.uiServicesProvider.updateSpecialBanner(this.specialBanners[3], this.specialEntities.find(entity => entity.id == this.fourthBannerId).type != null, this.fourthBannerId);
        await this.uiServicesProvider.updateSpecialBanner(this.specialBanners[4], this.specialEntities.find(entity => entity.id == this.fivethBannerId).type != null, this.fivethBannerId);
        await this.uiServicesProvider.updateSpecialBanner(this.specialBanners[5], this.specialEntities.find(entity => entity.id == this.sixthBannerId).type != null, this.sixthBannerId);
        this.spinner.hide();
        this.successfulDialog.show();
      }
      catch (error) {
        console.log(error);
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "      "
      }
      this.imageValidation.show();
    }
  }
  async updateSpecialPrograms() {
    if (this.firstProgramId != "" && this.secondProgramId != "" && this.thirdProgramId != "" && this.fourthProgramId != "" && this.fivethProgramId != "" && this.sixthProgramId != "") {
      this.spinner.show();
      try {
        await this.uiServicesProvider.updateSpecialProgram(this.specialPrograms[0], this.firstProgramId);
        await this.uiServicesProvider.updateSpecialProgram(this.specialPrograms[1], this.secondProgramId);
        await this.uiServicesProvider.updateSpecialProgram(this.specialPrograms[2], this.thirdProgramId);
        await this.uiServicesProvider.updateSpecialProgram(this.specialPrograms[3], this.fourthProgramId);
        await this.uiServicesProvider.updateSpecialProgram(this.specialPrograms[4], this.fivethProgramId);
        await this.uiServicesProvider.updateSpecialProgram(this.specialPrograms[5], this.sixthProgramId);
        this.spinner.hide();
        this.successfulDialog.show();
      }
      catch (error) {
        console.log(error);
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "      "
      }
      this.imageValidation.show();
    }
  }
  async updateChannelId() {
    if (this.channelId && this.channelId != "") {
      this.spinner.show();
      try {
        const done = await this.uiServicesProvider.updateChannelId(this.settings.id, this.channelId);
        this.spinner.hide();
        if (done) {
          this.successfulDialog.show();
        }
        else {
          this.errorDialog.show();
        }
      }
      catch (error) {
        this.spinner.hide();
        this.errorDialog.show();
      }
    }
    else {
      this.imageValidation.options = {
        title: "     "
      }
      this.imageValidation.show();
    }
  }
}
