import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomePageComponent } from './pages/home-page/home-page.component';
import { LiveStreamComponent } from './pages/live-stream/live-stream.component';
import { ProgramsComponent } from './pages/programs/programs.component';
import { VideosComponent } from './pages/videos/videos.component';
import { AboutUsComponent } from './pages/about-us/about-us.component';
import { EditAboutUsComponent } from './pages/edit-about-us/edit-about-us.component';
import { EditUseTermsComponent } from './pages/edit-use-terms/edit-use-terms.component';
import { UseTermsComponent } from './pages/use-terms/use-terms.component';
import { PrivacyTermsComponent } from './pages/privacy-terms/privacy-terms.component';
import { EditPrivacyComponent } from './pages/edit-privacy/edit-privacy.component';
import { ContactUsComponent } from './pages/contact-us/contact-us.component';
import { NewsComponent } from './pages/news/news.component';
import { ArticlesComponent } from './pages/articles/articles.component';
import { SportsComponent } from './pages/sports/sports.component';
import { LinksComponent } from './pages/links/links.component';
import { OurTeamComponent } from './pages/our-team/our-team.component';
import { AddOrRemoveProgramComponent } from './pages/add-or-remove-program/add-or-remove-program.component';
import { AddOrEditArticleComponent } from './pages/add-or-edit-article/add-or-edit-article.component';
import { AddOrEditLinksComponent } from './pages/add-or-edit-links/add-or-edit-links.component';
import { ProgramDetailsComponent } from './pages/program-details/program-details.component';
import { ProgramScheduleComponent } from './pages/program-schedule/program-schedule.component';
import { AddOrEditTeamMemberComponent } from './pages/add-or-edit-team-member/add-or-edit-team-member.component';
import { LoginPageComponent } from './pages/login-page/login-page.component';
import { LoginActivateService } from './services/uiServices/login-activate.service';
import { EditEventComponent } from './pages/edit-event/edit-event.component';
import { EventDetailsComponent } from './pages/event-details/event-details.component';
import { EditEventScheduleComponent } from './pages/edit-event-schedule/edit-event-schedule.component';
import { NotificationComponent } from './pages/notification/notification.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomePageComponent, canActivate: [LoginActivateService] },
  { path: 'live', component: LiveStreamComponent, canActivate: [LoginActivateService] },
  { path: 'programs', component: ProgramsComponent, canActivate: [LoginActivateService] },
  { path: 'video', component: VideosComponent, canActivate: [LoginActivateService] },
  { path: 'aboutUs', component: AboutUsComponent, canActivate: [LoginActivateService] },
  { path: 'aboutUsEdit', component: EditAboutUsComponent, canActivate: [LoginActivateService] },
  { path: 'editTerms', component: EditUseTermsComponent, canActivate: [LoginActivateService] },
  { path: 'terms', component: UseTermsComponent, canActivate: [LoginActivateService] },
  { path: 'privacy', component: PrivacyTermsComponent, canActivate: [LoginActivateService] },
  { path: 'editPrivacy', component: EditPrivacyComponent, canActivate: [LoginActivateService] },
  { path: 'contacts', component: ContactUsComponent, canActivate: [LoginActivateService] },
  { path: 'events', component: NewsComponent, canActivate: [LoginActivateService] },
  { path: 'articles', component: ArticlesComponent, canActivate: [LoginActivateService] },
  { path: 'sports', component: SportsComponent, canActivate: [LoginActivateService] },
  { path: 'links', component: LinksComponent, canActivate: [LoginActivateService] },
  { path: 'team', component: OurTeamComponent, canActivate: [LoginActivateService] },
  { path: 'editProgram/:id', component: AddOrRemoveProgramComponent, canActivate: [LoginActivateService] },
  { path: 'editLink/:id', component: AddOrEditLinksComponent, canActivate: [LoginActivateService] },
  { path: 'editMember/:id', component: AddOrEditTeamMemberComponent, canActivate: [LoginActivateService] },
  { path: 'editArticle/:type/:id', component: AddOrEditArticleComponent, canActivate: [LoginActivateService] },
  { path: 'programDetails/:id', component: ProgramDetailsComponent, canActivate: [LoginActivateService] },
  { path: 'programSchedule/:programId/:scheduleId', component: ProgramScheduleComponent, canActivate: [LoginActivateService] },
  { path: 'login', component: LoginPageComponent },
  { path: 'editEvent/:id', component: EditEventComponent, canActivate: [LoginActivateService] },
  { path: 'eventDetails/:id', component: EventDetailsComponent, canActivate: [LoginActivateService] },
  { path: 'eventSchedule/:eventId/:scheduleId', component: EditEventScheduleComponent, canActivate: [LoginActivateService] },
  { path: 'Notification', component: NotificationComponent },


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
