import { Component, OnInit, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { LinksService } from 'src/app/services/uiServices/links.service';
@Component({
  selector: 'app-links',
  templateUrl: './links.component.html',
  styleUrls: ['./links.component.css']
})
export class LinksComponent implements OnInit {
  @ViewChild('successfulDialog') private successfulDialog: SwalComponent;
  @ViewChild('errorDialog') private errorDialog: SwalComponent;
  @ViewChild('deleteSwal') private deleteSwal: SwalComponent;
  links: Array<Link>;
  constructor(private spinner: NgxSpinnerService, private linksService: LinksService) { }

  ngOnInit() {
    this.successfulDialog.options = {
      confirmButtonText: ""
    };
    this.errorDialog.options = {
      confirmButtonText: ""
    };
    this.loadLinks();
  }
  async deleteLink(linkId: string) {
    try {
      this.deleteSwal.options = {
        title: "  ",
        type: "warning",
        showCancelButton: true,
        cancelButtonText: "",
        confirmButtonText: ""
      };
      this.deleteSwal.show().then(value => {
        console.log(value);
        if (value.value === true) {
          this.linksService.deleteLink(linkId).then(success => {
            this.loadLinks()
          }, error => {
            console.log(error);
            this.errorDialog.show();
          }
          );
        }
      });

    }
    catch (error) {
      this.errorDialog.show();
    }
  }
  async loadLinks() {
    try {
      this.spinner.show();
      this.links = await this.linksService.getLinksList();
      this.spinner.hide();
    }
    catch (errror) {
      this.spinner.hide();
      this.errorDialog.show();
    }
  }
}
