import { Injectable, Inject } from '@angular/core';

import { ServiceHandlerProvider } from '../service-handler.service';
import { Constants } from 'src/app/Constants';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';

@Injectable({
  providedIn: 'root'
})
export class LinksService {
  userData: LoginResponse;

  constructor(private serviceHadler: ServiceHandlerProvider, @Inject(SESSION_STORAGE) private storage: StorageService) {
    this.userData = this.storage.get(Constants.USER_DATA);
  }
  async getLinksList(): Promise<Array<Link>> {
    return new Promise<Array<Link>>((resolve, reject) => {
      let query = `query{
        links{
          id,
          name,
          icon,
          url
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.links) {
          resolve(response.data.links);
        }
        else {
          reject("Failed to load links");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async deleteLink(linkId: string): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        deleteLink(where:{
          id:"${linkId}"
        }){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.deleteLink) {
          resolve(true);
        }
        else {
          reject("Failed to delete link");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async getLinkDetails(linkId: string): Promise<Link> {
    return new Promise<Link>((resolve, reject) => {
      let query = `query{
        link(where:{id:"${linkId}"}){
          id,
          name,
          url,
          icon
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.link) {
          resolve(response.data.link);
        }
        else {
          reject("Failed to load link");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async createLink(link: Link): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        createLink(
          data:{
            name:"${link.name}",
            url:"${link.url}"
            icon:"${link.icon}"
          }
        ){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.createLink) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async updateLink(link: Link): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        updateLink(
          where:{id:"${link.id}"},
          data:{
            name:"${link.name}",
            url:"${link.url}"
            icon:"${link.icon}"
          }
        ){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", this.userData.token, { "query": query }).subscribe((response) => {
        console.log(response);
        if (response.data && response.data.updateLink) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
}
